<template>
  <div class="content-body">
    <div class="body-box">
      <div class="body-module">
        <!-- 1 -->
        <img src="../assets/images/registerok.png" width="46px" height="46px" />
        <div style="margin-left:20px;padding-top:10px;">
          <h3 class="login-panel-title" autofocus>
            ,:
            <span>{{username}}</span>
          </h3>
          <div class="login-panel-title" autofocus>,{{tsecond}}</div>
        </div>
        <!-- 2 -->
      </div>
    </div>
  </div>
</template>

<script>
</script>

  <style  lang = "less" scoped>
@import "../less/registerok.less";
</style>
<script>
export default {
  name: 'retisterOk',
  data() {
    return {
      /*
      id:this.$route.params.id
      title:this.$route.params.title,
      */
      username: '',
      tsecond: 3
    }
  },
  //id
  mounted() {
    this.username = this.$route.params.username
    this.timer = setInterval(() => {
      this.tsecond--;
      if (this.tsecond === 0) {
        this.$router.push({ path: "/login", params: { username: this.username } })
      } // date
    }, 1000)
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer); // Vue
    }
  }

}
</script>
