<template>
  <div class="content-body">
    <a-form :form="form" @submit="handleSubmit">
      <a-divider orientation="left">
        <h4></h4>
      </a-divider>
      <a-row type="flex" justify="start">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-decorator="['nickname']" v-bind="formItemLayout">
            <label v-decorator="['nickname']">{{this.form.getFieldValue('nickname')}}</label>
          </a-form-item>
        </a-col>
        <a-col :span="0">
          <a-form-item>
            <a-input v-decorator="['id']"></a-input>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-radio-group v-decorator="['sex']">
              <a-radio :value="1"></a-radio>
              <a-radio :value="0"></a-radio>
            </a-radio-group>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start" :gutter="1">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-date-picker v-decorator="['birthday']" style="width: 100%" placeholder="" />
          </a-form-item>
        </a-col>
      </a-row>

      <a-divider orientation="left">
        <h4></h4>
      </a-divider>
      <a-row type="flex" justify="start" :gutter="1">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-input v-decorator="['phone']" placeholder=""></a-input>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-input v-decorator="['email']" placeholder=""></a-input>
          </a-form-item>
        </a-col>
      </a-row>

      <a-row type="flex" justify="start" :gutter="2">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-cascader
              v-decorator="['province']"
              :fieldNames="{ label: 'cname', value: 'id', children: 'items' }"
              :options="province"
              @change="provinceChange"
              placeholder="----"
              ref="province"
            ></a-cascader>
          </a-form-item>
        </a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-cascader
              v-decorator="['region']"
              :fieldNames="{ label: 'cname', value: 'id', children: 'items' }"
              :options="region"
              @change="regionChange"
              placeholder="----"
              ref="region"
            ></a-cascader>
          </a-form-item>
        </a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-cascader
              v-decorator="['city']"
              :fieldNames="{ label: 'cname', value: 'id', children: 'items' }"
              :options="city"
              @change="cityChange"
              placeholder="----"
              ref="city"
            ></a-cascader>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start">
        <a-col :span="3"></a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-button type="primary" htmlType="submit" class="button-primary"></a-button>
          </a-form-item>
        </a-col>
      </a-row>
    </a-form>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  name: 'userinfo',
  data() {
    return {
      formItemLayout: {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          }        }
      }, labelItemLayout: {
        labelCol: {
          xs: {
            span: 24,
            offset: 0,
          }
        }
      },
      province: [],
      region: [],
      city: [],
      pagestate: 'loading', //loading,loaded
      form: this.$form.createForm(this)
    }
  },
  created() {
    // 
    let iniProvince = this.axios.get( // 
      '/syscity', {
        params: { pid: 0, limit: 100, offset: 0 }
      }).then(result => {
        this.province = result.rows
      }).catch(error => {
        console.log(error)
      })

    // end
    const id = JSON.parse(this.$store.getters.userinfo).id
    // 
    if (id) {
      let iniUserInfo = iniProvince.then(() => {
        return this.axios.get('/users/' + id).then(result => { // 
          let { id, nickname, sex, phone, email, birthday, province, region, city } = result
          birthday = moment(birthday, 'YYYY-MM-DD')
          this.form.setFieldsValue({ id, nickname, sex, phone, email, birthday })
          this.$refs.province.setValue([province])
          return { province, region, city }
        }).catch(error => {
          return error
        })
      }).catch(error => console.log(error))

      let iniRegion = iniUserInfo.then(prcResult => { // 
        this.axios.get(
          '/syscity', {
            params: { pid: prcResult.province, limit: 100, offset: 0 }
          })
          .then(result => {
            this.region = result.rows
            this.$refs.region.setValue([prcResult.region])
          }).catch(error => {
            console.log(error)
          })
        return prcResult
      }).catch(error => console.log(error))

      iniRegion.then(prcResult => { // 
        this.axios.get(
          '/syscity', {
            params: { pid: prcResult.region, limit: 100, offset: 0 }
          })
          .then(result => {
            this.city = result.rows
            this.$refs.city.setValue([prcResult.city])
            this.pagestate = 'loaded'
          }).catch(error => {
            console.log(error)
          })
      }).catch(error => console.log(error))
    }
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => { // 
        if (!err) { // 
          this.confirmRegister(values)
        }
      })
    },
    moment,
    provinceChange(value) {
      if (this.pagestate === 'loading')
        return
      this.$refs.region.setValue([undefined])//value
      this.$refs.city.setValue([undefined])
      this.axios.get( // 
        '/syscity', {
          params: { pid: value[0], limit: 100, offset: 0 }
        })
        .then(result => {
          this.region = result.rows
        }).catch(error => {
          console.log(error)
        })
    },
    regionChange(value) {
      if (this.pagestate === 'loading')
        return
      this.$refs.city.setValue([undefined])
      this.axios.get( // 
        '/syscity', {
          params: { pid: value[0], limit: 100, offset: 0 }
        })
        .then(result => {
          this.city = result.rows
        }).catch(error => {
          console.log(error)
        })
    },
    cityChange(value) {
      // console.log('', value)
    },
    confirmRegister(values) {
      this.axios.patch(
        '/users/' + values.id, { // 
          sex: values.sex,
          birthday: values.birthday.format('YYYY-MM-DD'),
          phone: values.phone,
          email: values.email,
          province: values.province[0],
          region: values.region[0],
          city: values.city[0]
        }).then(result => {
          this.$message.success('')
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>
